import Blogs from './Blogs';
import useFetch from './useFetch';
const Home = () => {
const {data:blogs,isLoading,isError}=useFetch('http://localhost:8000/blogs');
    return ( 
        <div className="page">
            <div className="bg-photo-over"></div>
            <div className="bg-photo-container">
                <div className="bg-photo"></div>
            </div>
        <div className="page-body"> 
        <div className="center"> 
        
        {isLoading&& <div className='loading'><h1>Loading .......</h1></div>} 
        {isError && <div className='error'><h2> {isError}</h2></div>}  
         {blogs &&<Blogs blogs={blogs} title='All Blogs !' />}
          
        </div>
        </div> 
        </div>

     );
}
 
export default Home;