import { useHistory, useParams } from "react-router-dom";
import useFetch from "./useFetch";
const BlogDetails = () => {
    const {id}=useParams();
    const{data:blog,isLoading,isError}=useFetch('http://localhost:8000/blogs/'+ id );
    const history=useHistory();
    const handleDelete=()=>{
        fetch('http://localhost:8000/blogs/'+blog.id,{
            method:'DELETE'
        }).then(()=>{
            history.push('/');
        })
    }
    return ( 
        <div className="blog-details">   
                  {isLoading&& <div className='loading'><h1>Loading .......</h1></div>} 
                  {isError && <div className='error'><h2> {isError}</h2></div>} 
                  {blog&&(
                      <article>
                       <h2>{blog.title}</h2>
                       <p><span>written By { blog.author}</span></p> 
                       <div className='blog-body'>{blog.body}</div>
                      </article>
                  )}
                  <button onClick={handleDelete}>Delete</button>
        </div>
     );
}
 
export default BlogDetails
